/* UpdateSettings.css */

body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    background-color: #f4f6f8;
  }
  
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #f4f6f8;
  }
  
  .box {
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    padding: 2rem;
    width: 100%;
    max-width: 600px;
  }
  
  h4 {
    font-size: 1.8rem;
    font-weight: 500;
    color: #333333;
    text-align: center;
    margin-bottom: 1rem;
  }
  
  button {
    margin-top: 1rem;
    display: block;
    width: 100%;
    padding: 10px 20px;
    font-size: 1rem;
    border: none;
    border-radius: 4px;
    background-color: #1976d2;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #125a96;
  }
  
  button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
  
  .form-control {
    margin-bottom: 1rem;
  }
  
  .input-label {
    display: block;
    font-size: 1rem;
    font-weight: 500;
    margin-bottom: 0.5rem;
  }
  
  select, input {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #dddddd;
    border-radius: 4px;
    transition: border-color 0.3s ease;
  }
  
  select:focus, input:focus {
    border-color: #1976d2;
    outline: none;
  }
  
  .error-message {
    color: red;
    font-size: 0.9rem;
    margin-top: -0.5rem;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .footer {
    margin-top: 1rem;
    text-align: center;
    font-size: 0.9rem;
    color: #666666;
  }
  