/* General container styles */
.citizen-page-container {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
  }
  
  .back-button {
    position: absolute;
    top: 20px;
    left: 20px;
    padding: 10px 15px;
    font-size: 1rem;
    background-color: #f44336;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .back-button:hover {
    background-color: #d32f2f;
  }
  
  h2 {
    margin-top: 50px;
    font-size: 2.5rem;
    color: #333;
  }
  
  /* Dashboard section styles */
  .dashboard-sections {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
    margin-top: 30px;
  }
  
  .dashboard-card {
    flex: 1 1 300px;
    min-width: 250px;
    max-width: 350px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
    cursor: pointer;
  }
  
  .dashboard-card:hover {
    transform: translateY(-5px);
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.15);
  }
  
  .dashboard-card h3 {
    font-size: 1.5rem;
    color: #333;
    margin-bottom: 10px;
  }
  
  .dashboard-card p {
    color: #666;
    font-size: 1rem;
    margin-bottom: 20px;
  }
  
  /* Button styles with hover effect */
  .action-button {
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
  }
  
  .action-button:hover {
    background-color: #0056b3;
    transform: scale(1.05);
  }
  
  /* Footer section */
  .footer {
    margin-top: 40px;
    padding: 20px;
    background-color: #e9e9e9;
    border-radius: 8px;
  }
  
  .footer h3 {
    font-size: 1.2rem;
    color: #333;
  }
  
  .footer p {
    font-size: 1rem;
    color: #555;
  }
  
  .footer a {
    color: #007bff;
    text-decoration: none;
  }
  
  .footer a:hover {
    text-decoration: underline;
  }
  