/* Background image for the entire page */
body {
  background: url('../images/background.jpg') no-repeat center center fixed;
  background-size: cover;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

/* Main container with padding and no background color */
.homepage-container {
  padding: 30px;
  font-family: Arial, sans-serif;
  max-width: 1200px;
  margin: 30px auto;
  border-radius: 10px;
  color: #ffffff; /* White text for readability */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7); /* Text shadow for contrast */
}

.homepage-title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2.5rem; /* Larger title font */
  color: #ffffff;
}

.intro-content h3,
.container-item h3,
.get-involved h3 {
  font-size: 1.8rem;
  margin-bottom: 15px;
  color: #FFD700; /* Golden color for emphasis */
}

.intro-content p,
.container-item p,
.get-involved p,
footer p {
  font-size: 1.1rem;
  line-height: 1.7;
  color: #ffffff;
}

/* Buttons with contrasting colors */
.learn-more-button,
.container-button,
.get-involved-button {
  padding: 12px 20px;
  font-size: 1rem;
  background-color: #FFD700; /* Golden color for buttons */
  color: #333333; /* Dark text for contrast */
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.learn-more-button:hover,
.container-button:hover,
.get-involved-button:hover {
  background-color: #FFA500; /* Darker golden for hover effect */
  transform: translateY(-2px);
}

/* Enhanced image styling */
.intro-image,
.observer-image {
  width: 100%;
  max-width: 400px; /* Restricts the max size */
  border-radius: 20px; /* Rounded corners */
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.3); /* Add shadow for depth */
  transition: transform 0.3s ease-in-out, filter 0.3s ease-in-out; /* Smooth animation */
  margin: 20px auto; /* Center images with top margin */
}

/* Hover effect for images */
.intro-image:hover,
.observer-image:hover {
  transform: scale(1.05); /* Slight zoom effect on hover */
  filter: brightness(1.1) contrast(1.2); /* Brighten and increase contrast */
}

/* Adding fade-in animation for images */
@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.intro-image,
.observer-image {
  animation: fadeIn 1s ease-in;
}

footer {
  text-align: center;
  margin-top: 30px;
}

footer a {
  color: #FFD700; /* Golden color for links */
  text-decoration: underline;
}

footer a:hover {
  color: #FFA500;
}
