.report-container {
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .back-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 20px;
    width: 70px;

  }
  
  .back-button:hover {
    background-color: #0056b3;
  }
  
  .report-title {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .report-summary {
    font-size: 16px;
    margin-bottom: 20px;
  }
  
  .chart-container {
    margin-bottom: 20px;
  }
  
  .key-metrics, .recommendations, .report-conclusion {
    margin: 20px 0;
  }
  
  h3 {
    margin-top: 15px;
  }
  