/* General container styles */
.election-observer-container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.back-button {
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 10px 15px;
  font-size: 1rem;
  background-color: #f44336;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.back-button:hover {
  background-color: #d32f2f;
}

h2 {
  margin-top: 50px;
  font-size: 2.5rem;
  color: #333;
}

.welcome-message {
  font-size: 1.2rem;
  color: #666;
  margin-bottom: 30px;
}

/* Grid Layout for Content */
.content-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-top: 20px;
}

/* Card styles */
.card {
  padding: 20px;
  border-radius: 8px;
  background-color: #f9f9f9;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.15);
}

.card h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 10px;
}

.card ul {
  list-style-type: none;
  padding: 0;
}

.card ul li {
  font-size: 1rem;
  color: #555;
  margin-bottom: 10px;
}

/* Button styles with hover effect */
.card button {
  width: 100%;
  padding: 10px 15px;
  font-size: 1rem;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.card button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

/* Footer section */
.footer {
  margin-top: 40px;
  padding: 20px;
  background-color: #e9e9e9;
  border-radius: 8px;
  text-align: center;
}

.footer p {
  font-size: 1rem;
  color: #555;
}

.footer a {
  color: #007bff;
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}
