/* src/pages/VotingHistory.css */

.voting-history-container {
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  .back-button {
    margin-bottom: 20px;
    padding: 10px 15px;
    background-color: #007bff; /* Bootstrap primary color */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    width: 70px;

  }
  
  .back-button:hover {
    background-color: #0056b3; /* Darker shade for hover effect */
  }
  
  .voting-history-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
  }
  
  .voting-history-table th, .voting-history-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  .voting-history-table th {
    background-color: #f2f2f2;
  }
  