/* General Page Styling */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f8f9fa; /* Light gray for a modern look */
  }
  
  /* Main Container */
  .view-results-container {
    width: 90%;
    max-width: 1200px;
    margin: 40px auto;
    padding: 20px;
    background: #ffffff; /* White background for contrast */
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15); /* Soft shadow for depth */
    animation: fadeIn 1s ease-in-out;
  }
  
  /* Title Styling */
  .animated-title {
    text-align: center;
    font-size: 2.5rem; /* Large title */
    margin-bottom: 20px;
    color: #343a40; /* Dark gray for good readability */
    font-weight: bold;
    animation: slideDown 1s ease-in-out;
  }
  
  /* Table Styling */
  .party-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
    text-align: center;
    font-size: 1rem;
  }
  
  .party-table th,
  .party-table td {
    border: 1px solid #ddd;
    padding: 12px; /* Spacious padding for clarity */
  }
  
  .party-table th {
    background-color: #007bff; /* Primary blue color */
    color: white; /* White text for contrast */
    font-size: 1.1rem;
    text-transform: uppercase;
  }
  
  .party-table td {
    background-color: #f8f9fa; /* Subtle light gray for readability */
    color: #212529; /* Dark text for contrast */
  }
  
  .party-row:nth-child(even) {
    background-color: #e9ecef; /* Alternating row color for clarity */
  }
  
  .party-row:hover {
    background-color: #d4edda; /* Light green highlight on hover */
    transition: background-color 0.3s ease-in-out;
  }
  
 
  
  /* Animations */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: scale(0.95);
    }
    to {
      opacity: 1;
      transform: scale(1);
    }
  }
  
  @keyframes slideDown {
    from {
      transform: translateY(-20px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  