/* General layout and typography */
.admin-page-container {
  padding: 20px;
  font-family: Arial, sans-serif;
  background: url'src/images/admin.jpg'; /* Background image */
  background-size: cover; /* Ensure the image covers the entire container */
  color: #ffffff; /* Set text color to white for visibility */
  text-align: center;
}

.admin-page-container h2 {
  font-size: 2em;
  margin-bottom: 20px;
}

.dashboard-sections {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

/* Section containers styling */
.section-container {
  width: 250px;
  height: 220px;
  background-color: rgba(255, 255, 255, 0.85); /* Slightly transparent white background */
  border-radius: 8px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  text-align: left;
  position: relative;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.section-container:hover {
  transform: translateY(-5px);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.2);
}

.section-container h3 {
  font-size: 1.3em;
  margin-bottom: 10px;
  color: #000; /* Set header color to black for better contrast */
}

.section-container p {
  font-size: 0.9em;
  color: #555; /* Dark gray color for text */
}

/* Button styling */
.button-group {
  display: flex;
  gap: 10px;
}

.action-button {
  background-color: #4CAF50;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.action-button:hover {
  background-color: #45a049;
  transform: scale(1.05);
}

/* Back button styling */
.back-button {
  background-color: #555;
  color: white;
  padding: 8px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 20px;
  width: 70px;
}

.back-button:hover {
  background-color: #444;
}
