.poll-vote-container {
    text-align: center;
    padding: 20px;
  }
  
  .party-list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .party-card {
    border: 2px solid #ccc;
    padding: 20px;
    width: 200px;
    cursor: pointer;
    transition: transform 0.2s;
  }
  
  .party-card:hover {
    transform: scale(1.05);
  }
  
  .party-card.selected {
    border-color: green;
    background-color: #f0f8ff;
  }
  
  .submit-vote-button,
  .back-button {
    margin: 20px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .submit-vote-button {
    background-color: blue;
    color: white;
  }
  
  .back-button {
    background-color: grey;
    color: white;
  }
  