.access-container {
  display: flex; /* Flexbox layout */
  flex-wrap: wrap; /* Allow items to wrap to the next line */
  justify-content: center; /* Center the items horizontally */
  align-items: center; /* Center the items vertically */
  gap: 20px; /* Space between the cards */
  padding: 40px;
  background: url'src/images/admin.jpg';; /* Background image */
  background-size: cover; /* Ensure the image covers the entire container */
  min-height: 100vh; /* Ensure full height for the container */
  color: #ffffff; /* Set default text color to white */
}

.access-card {
  background: linear-gradient(135deg, rgba(78, 84, 200, 0.85), rgba(143, 148, 251, 0.85)); /* Transparent gradient effect */
  color: black;
  border-radius: 15px;
  padding: 20px 30px;
  width: 250px;
  text-align: center;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
  overflow: hidden; /* Prevent content overflow */
  position: relative; /* For animations */
}

.access-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1); /* Subtle overlay */
  mix-blend-mode: overlay;
  pointer-events: none; /* Prevent interaction */
}

.access-card:hover {
  transform: translateY(-10px); /* Lift effect on hover */
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.2); /* Enhanced shadow */
  background: linear-gradient(135deg, #4e54c8, #6368f7); /* Darker hover gradient */
}

.access-card h3 {
  font-size: 1.8rem;
  margin-bottom: 20px;
  color: #ffffff; /* Ensure text is white for contrast */
}

.access-button {
  background-color: #f5f5f5; /* White button */
  color: #4e54c8; /* Matches gradient */
  border: none;
  border-radius: 25px;
  padding: 10px 20px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  margin: 10px 0;
  transition: background-color 0.3s ease, color 0.3s ease, transform 0.2s ease;
}

.access-button:hover {
  background-color: #4e54c8; /* Button color switch */
  color: #ffffff; /* Contrast text on hover */
  transform: scale(1.05); /* Slight enlarge effect */
}

.access-button:active {
  transform: scale(0.95); /* Press effect */
}

/* Force 2 cards per row with flex-basis */
.access-card {
  flex: 1 1 calc(50% - 40px); /* 50% width minus gap spacing */
  max-width: 300px; /* Limit max width */
}

/* Responsive adjustment for smaller screens */
@media (max-width: 768px) {
  .access-card {
    flex: 1 1 100%; /* Full width for smaller screens */
    max-width: 90%; /* Adjust max width */
  }
}
