/* src/pages/ReportIssue.css */

.report-issue-container {
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  .back-button {
    margin-bottom: 20px;
    padding: 10px 15px;
    background-color: #007bff; /* Bootstrap primary color */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    width: 70px;

  }
  
  .back-button:hover {
    background-color: #0056b3; /* Darker shade for hover effect */
  }
  
  .report-issue-form {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
  }
  
  .report-issue-form label {
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  .report-issue-form textarea {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: none;
  }
  
  .submit-button {
    margin-top: 10px;
    padding: 10px 15px;
    background-color: #28a745; /* Success color */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .submit-button:hover {
    background-color: #218838; /* Darker shade for hover effect */
  }
  