/* General container styling */
.data-analyst-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
  color: #333;
  text-align: center;
}

.back-button {
  position: absolute;
  top: 20px;
  left: 20px;
  background-color: #4CAF50;
  
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.back-button:hover {
  background-color: #45a049;
}

h2 {
  font-size: 2em;
  margin-bottom: 10px;
  color: #444;
}

.welcome-message {
  font-size: 1.1em;
  color: #555;
  margin-bottom: 30px;
}

/* Grid layout for content sections */
.content-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

/* Card style for each section */
.card {
  background-color: #FFCF9D; /* Light peach background */
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.15);
}

.analysis-section h3 {
  font-size: 1.5em;
  color: #333;
  margin-bottom: 10px;
}

.analysis-section p {
  font-size: 1em;
  color: #666;
  margin-bottom: 20px;
}

/* Button styles */
.action-button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1em;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.action-button:hover {
  background-color: #0056b3;
  transform: translateY(-2px);
}

.action-button:focus {
  outline: none;
  box-shadow: 0 0 3px 2px rgba(0, 123, 255, 0.5);
}

/* Footer section */
.footer {
  margin-top: 40px;
  padding: 20px;
  background-color: #FFCF9D; /* Light peach background */
  border-radius: 8px;
  font-size: 0.9em;
  color: #555;
}

.footer h3 {
  font-size: 1.2em;
  color: #333;
  margin-bottom: 8px;
}

.footer a {
  color: #007bff;
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}

/* Responsive Design */
@media (max-width: 768px) {
  .back-button {
    position: static;
    display: block;
    margin: 10px auto;
  }

  .welcome-message {
    font-size: 1em;
  }

  .content-grid {
    grid-template-columns: 1fr;
  }

  .action-button {
    width: 100%;
    padding: 12px;
    font-size: 1em;
  }
}
