/* src/components/Navbar.css */

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: #3498DB;
}

.logo-container {
  display: flex;
  align-items: center;
}

.navbar-logo {
  height: 50px;
  width: auto;
  margin-right: 20px;
}

.navbar-title {
  font-size: 24px;
  color: white;
  font-weight: bold;
}

.navbar-list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.navbar-item {
  margin-right: 20px;
}

.navbar-link {
  color: white;
  text-decoration: none;
  font-size: 18px;
  transition: color 0.3s ease;
}

.navbar-link:hover {
  color: #d1ecf1;
}

.active-link {
  font-weight: bold;
  border-bottom: 2px solid white;
}
